.stack-container {
  display: flex;
  width: 95%;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin: 20px;
  h1 {
    font-family: "Comfortaa";
    font-size: 45px;
    margin-top: 30px;
    letter-spacing: 7px;
    color: #b23a6c;
  }
  .stack-wrapper {
    display: flex;
    justify-content: center;
    flex-direction: row;
    width: 100%;
    .front, .back, .tools {
      width: 30%;
      margin: 20px;
      border-radius: 10px;
      background-size: 200% auto;
      background-image: linear-gradient(#ffffff9c, #ffffff9c), linear-gradient(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
      background-image: -webkit-linear-gradient(#ffffff9c, #ffffff9c), -webkit-linear-gradient(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
      background-image: -moz-linear-gradient(#ffffff9c, #ffffff9c), -moz-linear-gradient(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
      background-image: -o-(#ffffff9c, #ffffff9c), -o-(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
      background-origin: border-box;
      background-clip: content-box, border-box;
      box-shadow: 6px 10px 17px 0px #000000ab;
      letter-spacing: 2px;
      .top-div {
        background-image: linear-gradient(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
        background-image: -moz-linear-gradient(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
        background-image: -o-(to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
        background-image: -webkit-gradient(linear, to right, #26bd9f 0%, #8b7ae2 51%, #26bd9f 100%);
        background-size: 200% auto;
        height: 15%;
        font-family: "Comfortaa";
        font-size: 27px;
        color: white;
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px 10px 0 0;
      }
      .bottom-div {
        position: relative;
        padding: 15px;
        height: 500px;
        .stack-list{
          display: flex;
          flex-direction: row;
          width: 100%;
          .column {
            display: flex;
            flex-direction: column;
            flex-basis: 100%;
            flex: 1;
            width: 50%;
            .language {
              font-family: "Comfortaa";
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: space-evenly;
              padding: 10px;
              margin: 10px;
              .stack-pic {
                height: 70px;
              }
            }
          }
        }
      }
    }
  }
}


// ********** MEDIA QUERIES **********

// TABLET: potrait
@media screen and (max-width: 992px) {
  .stack-container {
    width: 100%;
    .stack-wrapper {
      .front, .back, .tools {
        .top-div {
          font-size:30px;
        }

        .bottom-div {
          .stack-list {
            .column {
              .language {
                margin: 16px;
                font-size: 14px;
                .stack-pic {
                  height: 55px;
                }
              }
            }
          }
        }
      }
    }
  }
}

// PHONE: potrait
@media screen and (max-width: 480px) {
  .stack-container {

    .stack-wrapper {
      flex-direction: column;
      align-items: center;

      .front, .back, .tools {
        width: 90%;
        .top-div {
          padding: 14px;
        }
        .bottom-div {
          .stack-list {
            .column {
              .language {
                margin: 15px;
                font-size: 15px;
                .stack-pic {
                  height: 58px;
                }
              }
            }
          }
        }
      }

    }
  }
}
